body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
h2.help-app-title {
  font-size: 3em;
}
.styles_modal__gNwvD {
  padding: 5% 7% 7% 7%;
}
h3.help-app-under-the-hood-title {
  font-size: 2em;
}
.styles_overlay__CLSq- {
  background: rgba(255, 255, 255, 0.75);
}
.styles_modal__gNwvD{
  box-shadow: 0px 0px 15px 0 rgba(0, 0, 0, 0.25) !important;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#mnist-canvas{
    background: #efefef;
    border-radius: 6px;
    box-shadow: inset 0 0 6px 0px #00000030;  
    max-width: 350px;
    max-height: 350px;
    min-height: 350px;
    min-width: 350px;
    margin-left: 10%;
}
.writing-canvas{
    background: #efefef; 
    border-radius: 6px;
    box-shadow: inset 0 0 6px 0px #00000030; 
    cursor: crosshair;
}

.clear-button{
    /*background: black;*/
    background: #2295de;
    border: 0;
    color: white;
    padding: 16px 42px;
    display: block;
    font-size: 1.3em; 
    border-radius: 20em;
    transition: 0.15s ease-in;
    cursor:pointer;
    outline: 0 none;
    margin:auto;
}
.tooltip {
    background: #ffffffb5;
    box-shadow: 0 0 2px 1px #0000001f;
    padding: 2%;
    margin-top: 3%;
}
.clear-button:hover{
    box-shadow: 0 3px 6px 2px #0000004f;

}

.save-button{
    background: #2295de;
    border: 0;
    color: white;
    padding: 16px 42px;
    font-size: 1.3em; 
    border-radius: 20em;
    transition: 0.15sss ease-in;
    cursor:pointer;
    outline: 0 none;
}

.save-button:hover{
    box-shadow: 0 3px 6px 2px #0000004f;

}
h2.col-sm-12.align-self-start {
    font-size: 1.4em;
    font-weight: 100;
    letter-spacing: 0.1em;
}
.clear-button-row{
    margin-top: 5%;
}
